
  .slider
    min-height: 1px
    width: 100%;
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      width: 100%;
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        width: 100%
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      transform: translateZ(1px)
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
